import { Component, OnInit, Input, ViewChild, ElementRef, AfterViewInit, OnDestroy } from '@angular/core';
import { PositionsService } from 'src/app/shared/classes/positions.service';
import { Position } from '../../../shared/interfaces';
import { MaterialService, MaterialInstance } from 'src/app/shared/classes/material.services';
import { FormGroup, FormControl, Validators } from '@angular/forms';
@Component({
  selector: 'app-positions-form',
  templateUrl: './positions-form.component.html',
  styleUrls: ['./positions-form.component.css']
})
// AfterViewInit -        
// OnDestroy - 
export class PositionsFormComponent implements OnInit, AfterViewInit, OnDestroy {
  //    
  @Input('categoryId') categoryId: string
  //     
  @ViewChild('modal') modalRef: ElementRef
  positions: Position[] = []
  loading = false
  modal: MaterialInstance
  form: FormGroup
  //       
  positionId = null

  constructor(private positionsService: PositionsService) { }

  ngOnInit() {
    this.form = new FormGroup({
      name: new FormControl(null, Validators.required),
      cost: new FormControl(null, [Validators.required, Validators.min(1)])
    })

    this.loading = true
    // debugger -   
    // debugger
    this.positionsService.fetch(this.categoryId).subscribe(
      positions => {
        this.positions = positions
        this.loading = false
      }
    )
  }

  ngOnDestroy() {
    //     
    this.modal.destroy()
  }

  ngAfterViewInit() {
    //          
    this.modal = MaterialService.initModal(this.modalRef)
  }

  //     
  onSelectPosition(position: Position){
    //   
    this.positionId = position._id
    //    
    this.form.patchValue({
      name: position.name,
      cost: position.cost
    })
    this.modal.open()
    //    
    MaterialService.updateTextInputs()

  }
  //    
  onAddPosition(){
    //  
    this.positionId = null
    //    
    this.form.reset({
      name: '',
      cost: 1
    })
    this.modal.open()
    //    
    MaterialService.updateTextInputs()
    this.modal.open()
    //     
  }
  //  
  onCancel(){
    this.modal.close()
  }
  //  
  onDeletePosition(event: Event ,position: Position){
    //  
    event.stopPropagation()

    const decision = window.confirm(`  "${position.name}" ?`);
    if(decision){
      this.positionsService.delete(position).subscribe(
        res => {
          //  
          const idx = this.positions.findIndex( p => p._id === position._id)
          this.positions.splice(idx, 1);
          MaterialService.toast(res.message)
        },
        error => MaterialService.toast(error.error.message)
      )
    }
  }

  //  
  onSubmit(){
    //     
    this.form.disable()
    const position: Position = {
      name: this.form.value.name,
      cost: this.form.value.cost,
      category: this.categoryId
    }

    if(this.positionId) {
      position._id = this.positionId
      this.positionsService.update(position).subscribe(
        newPosition => {
          //     
          const idx = this.positions.findIndex( p => p._id === newPosition._id)
          this.positions[idx] = newPosition
          MaterialService.toast(' ')
        },
        error => {
          this.form.enable()
          MaterialService.toast(error.error.message)
        },
        () => {
          this.modal.close()
          this.form.enable()
          this.form.reset({name: '', cost: '1'})
        }
      )
    } else {
      //  
      this.positionsService.create(position).subscribe(
        newPosition => {
          MaterialService.toast(' ')
          //      
          this.positions.push(newPosition)
        },
        error => {
          this.form.enable()
          MaterialService.toast(error.error.message)
        },
        () => {
          this.modal.close()
          this.form.enable()
          this.form.reset({name: '', cost: '1'})
        }
      )
    }
  }



}
