import { Component, OnInit, ViewChild, ElementRef, OnDestroy, AfterViewInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { MaterialService, MaterialInstance } from '../shared/classes/material.services';
import { OrderService } from './order.service';
import { OrderPosition, Order } from '../shared/interfaces';
import { OrdersService } from '../shared/services/orders.service';
import { Subscription } from 'rxjs';

// providers: [OrderService] -     
@Component({
  selector: 'app-order-page',
  templateUrl: './order-page.component.html',
  styleUrls: ['./order-page.component.css'],
  providers: [OrderService]
})
//OnDestroy -    
//AfterViewInit -         
export class OrderPageComponent implements OnInit, OnDestroy, AfterViewInit {

  @ViewChild('modal') modalRef: ElementRef
  //      
  isRoot: boolean
  modal: MaterialInstance
  //      
  panding = false
  oSub: Subscription

  //            
  constructor(private router: Router,
              private order: OrderService,
              private ordersService: OrdersService ) { }

  ngOnInit() {
    this.isRoot = this.router.url === '/order'
    //     
    this.router.events.subscribe(
      event => {
        //    false     
        // console.log(Event,event)
        //       
        //        
        if(event instanceof NavigationEnd){
          this.isRoot = this.router.url === '/order'
        }

      }
    )
  }

  ngOnDestroy(){
    //   
    this.modal.destroy()
    if(this.oSub){
      //   
      this.oSub.unsubscribe()
    }
  }

  ngAfterViewInit(){
    //   
    this.modal = MaterialService.initModal(this.modalRef)
  }

  open(){
    this.modal.open()
  }

  cancel(){
    this.modal.close()
  }

  submit(){
    this.panding = true
    //   
    //         
    const order: Order = {
      list: this.order.list.map( item => {
        //     
        delete item._id
        return item
      })
    }
    this.oSub = this.ordersService.create(order).subscribe(
      (newOrder) => {
        MaterialService.toast(`  ${newOrder.order}`)
        this.order.clear()
      },
      error => {MaterialService.toast(error.error.message)},
      () => {
        this.modal.close()
        this.panding = true
      }
    )

  }

  removePosition(orderPosition: OrderPosition){
    this.order.remove(orderPosition)
    MaterialService.toast(`${orderPosition.name} .`)
  }
}
